/* ======================================================
    <!-- 表单（第三方插件） Form ( 3rd Party Plugin ) -->
/* ====================================================== */
@import '@uixkit/core/_global/scss/_variable-and-mixin.scss';
@import '_variable-and-mixin.scss';
/*
 * 1. 日期选择器
*/
/*
 ---------------------------
 1. 日期选择器（第三者插件）
 ---------------------------
 */
.uix-controls__date::after {
    font-family: 'Font Awesome 5 Free';
    font-size: 1rem;
    font-weight: 600;
    position: absolute;
    top: 50%;
    right: 1rem;
    display: inline-block;
    content: "\f784";
    transform: translateY(-50%);
    color: $control-sub-color;
}
.xdsoft_datetimepicker {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    position: absolute;
    z-index: 9999;
    display: none;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 8px;
    padding-top: 2px;
    padding-left: 0;
    color: #333333;
    border-top: 1px solid #cccccc;
    border-right: 1px solid #cccccc;
    border-bottom: 1px solid #bbbbbb;
    border-left: 1px solid #cccccc;
    background: #ffffff;
    box-shadow: 0 5px 15px -5px rgba(0, 0, 0, 0.506);
}
.xdsoft_datetimepicker.xdsoft_rtl {
    padding: 8px 0 8px 8px;
}
.xdsoft_datetimepicker iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 75px;
    height: 210px;
    border: none;
    background: transparent;
}
/*For IE8 or lower*/
.xdsoft_datetimepicker button {
    border: none !important;
}
.xdsoft_noselect {
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    -webkit-touch-callout: none;
}
.xdsoft_noselect::selection {
    background: transparent
}
.xdsoft_noselect::-moz-selection {
    background: transparent
}
.xdsoft_datetimepicker.xdsoft_inline {
    position: static;
    display: inline-block;
    box-shadow: none;
}
.xdsoft_datetimepicker * {
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.xdsoft_datetimepicker .xdsoft_datepicker, .xdsoft_datetimepicker .xdsoft_timepicker {
    display: none;
}
.xdsoft_datetimepicker .xdsoft_datepicker.active, .xdsoft_datetimepicker .xdsoft_timepicker.active {
    display: block;
}
.xdsoft_datetimepicker .xdsoft_datepicker {
    float: left;
    width: 224px;
    margin-left: 8px;
}
.xdsoft_datetimepicker.xdsoft_rtl .xdsoft_datepicker {
    float: right;
    margin-right: 8px;
    margin-left: 0;
}
.xdsoft_datetimepicker.xdsoft_showweeks .xdsoft_datepicker {
    width: 256px;
}
.xdsoft_datetimepicker .xdsoft_timepicker {
    float: left;
    width: 58px;
    margin-top: 0;
    margin-left: 8px;
    text-align: center;
}
.xdsoft_datetimepicker.xdsoft_rtl .xdsoft_timepicker {
    float: right;
    margin-right: 8px;
    margin-left: 0;
}
.xdsoft_datetimepicker .xdsoft_datepicker.active + .xdsoft_timepicker {
    margin-top: 8px;
    margin-bottom: 3px
}
.xdsoft_datetimepicker .xdsoft_monthpicker {
    position: relative;
    text-align: center;
}
.xdsoft_datetimepicker .xdsoft_label i,
.xdsoft_datetimepicker .xdsoft_prev,
.xdsoft_datetimepicker .xdsoft_next,
.xdsoft_datetimepicker .xdsoft_today_button {
    background-image: url(data:image/png;base64,);
}
.xdsoft_datetimepicker .xdsoft_label i {
    display: inline-block;
    width: 9px;
    height: 20px;
    vertical-align: middle;
    opacity: 0.5;
    background-position: -92px -19px;
}
.xdsoft_datetimepicker .xdsoft_prev {
    float: left;
    background-position: -20px 0;
}
.xdsoft_datetimepicker .xdsoft_today_button {
    float: left;
    margin-left: 5px;
    background-position: -70px 0;
}
.xdsoft_datetimepicker .xdsoft_next {
    float: right;
    background-position: 0 0;
}
.xdsoft_datetimepicker .xdsoft_next,
.xdsoft_datetimepicker .xdsoft_prev,
.xdsoft_datetimepicker .xdsoft_today_button {
    position: relative;
    display: block;
    overflow: hidden;
    width: 20px;
    min-width: 0;
    height: 30px;
    padding: 0;
    cursor: pointer;
    white-space: nowrap;
    text-indent: 100%;
    opacity: 0.5;
    border: 0 none;
    outline: medium none;
    background-color: transparent;
    background-repeat: no-repeat;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
}
.xdsoft_datetimepicker .xdsoft_timepicker .xdsoft_prev,
.xdsoft_datetimepicker .xdsoft_timepicker .xdsoft_next {
    display: block;
    float: none;
    width: 30px;
    height: 15px;
    margin-top: 7px;
    margin-left: 14px;
    background-position: -40px -15px;
}
.xdsoft_datetimepicker.xdsoft_rtl .xdsoft_timepicker .xdsoft_prev,
.xdsoft_datetimepicker.xdsoft_rtl .xdsoft_timepicker .xdsoft_next {
    float: none;
    margin-right: 14px;
    margin-left: 0;
}
.xdsoft_datetimepicker .xdsoft_timepicker .xdsoft_prev {
    margin-top: 0;
    margin-bottom: 7px;
    background-position: -40px 0;
}
.xdsoft_datetimepicker .xdsoft_timepicker .xdsoft_time_box {
    overflow: hidden;
    height: 151px;
    border-bottom: 1px solid #dddddd;
}
.xdsoft_datetimepicker .xdsoft_timepicker .xdsoft_time_box > div > div {
    font-size: 12px;
    line-height: 25px;
    height: 25px;
    border-collapse: collapse;
    cursor: pointer;
    text-align: center;
    color: #666666;
    border-top: 1px solid #dddddd;
    border-bottom-width: 0;
    background: #f5f5f5;
}
.xdsoft_datetimepicker .xdsoft_timepicker .xdsoft_time_box > div > div:first-child {
    border-top-width: 0;
}
.xdsoft_datetimepicker .xdsoft_today_button:hover,
.xdsoft_datetimepicker .xdsoft_next:hover,
.xdsoft_datetimepicker .xdsoft_prev:hover {
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}
.xdsoft_datetimepicker .xdsoft_label {
    font-size: 14px;
    font-weight: bold;
    line-height: 20px;
    position: relative;
    z-index: 9999;
    display: inline;
    float: left;
    width: 182px;
    margin: 0;
    padding: 5px 3px;
    cursor: pointer;
    text-align: center;
    background-color: #ffffff;
}
.xdsoft_datetimepicker .xdsoft_label:hover > span {
    text-decoration: underline;
}
.xdsoft_datetimepicker .xdsoft_label:hover i {
    opacity: 1.0;
}
.xdsoft_datetimepicker .xdsoft_label > .xdsoft_select {
    position: absolute;
    z-index: 101;
    top: 30px;
    right: 0;
    display: none;
    overflow-y: hidden;
    max-height: 160px;
    border: 1px solid #cccccc;
    background: #ffffff;
}
.xdsoft_datetimepicker .xdsoft_label > .xdsoft_select.xdsoft_monthselect {
    right: -7px
}
.xdsoft_datetimepicker .xdsoft_label > .xdsoft_select.xdsoft_yearselect {
    right: 2px
}
.xdsoft_datetimepicker .xdsoft_label > .xdsoft_select > div > .xdsoft_option:hover {
    color: #ffffff;
    background: #ff8000;
}
.xdsoft_datetimepicker .xdsoft_label > .xdsoft_select > div > .xdsoft_option {
    padding: 2px 10px 2px 5px;
    text-decoration: none !important;
}
.xdsoft_datetimepicker .xdsoft_label > .xdsoft_select > div > .xdsoft_option.xdsoft_current {
    font-weight: 700;
    color: #ffffff;
    background: #33aaff;
    box-shadow: #178fe5 0 1px 3px 0 inset;
}
.xdsoft_datetimepicker .xdsoft_month {
    width: 100px;
    text-align: right;
}
.xdsoft_datetimepicker .xdsoft_calendar {
    clear: both;
}
.xdsoft_datetimepicker .xdsoft_year {
    width: 48px;
    margin-left: 5px;
}
.xdsoft_datetimepicker .xdsoft_calendar table {
    width: 100%;
    border-collapse: collapse;
}
.xdsoft_datetimepicker .xdsoft_calendar td > div {
    padding-right: 5px;
}
.xdsoft_datetimepicker .xdsoft_calendar th {
    height: 25px;
}
.xdsoft_datetimepicker .xdsoft_calendar td, .xdsoft_datetimepicker .xdsoft_calendar th {
    font-size: 12px;
    width: 14.2857142%;
    height: 25px;
    padding: 0;
    border-collapse: collapse;
    cursor: pointer;
    text-align: right;
    vertical-align: middle;
    color: #666666;
    border: 1px solid #dddddd;
    background: #f5f5f5;
}
.xdsoft_datetimepicker.xdsoft_showweeks .xdsoft_calendar td, .xdsoft_datetimepicker.xdsoft_showweeks .xdsoft_calendar th {
    width: 12.5%;
}
.xdsoft_datetimepicker .xdsoft_calendar th {
    background: #f1f1f1;
}
.xdsoft_datetimepicker .xdsoft_calendar td.xdsoft_today {
    color: #33aaff;
}
.xdsoft_datetimepicker .xdsoft_calendar td.xdsoft_highlighted_default {
    color: #000000;
    background: #ffe9d2;
    box-shadow: #ffb871 0 1px 4px 0 inset;
}
.xdsoft_datetimepicker .xdsoft_calendar td.xdsoft_highlighted_mint {
    color: #000000;
    background: #c1ffc9;
    box-shadow: #00dd1c 0 1px 4px 0 inset;
}
.xdsoft_datetimepicker .xdsoft_calendar td.xdsoft_default,
.xdsoft_datetimepicker .xdsoft_calendar td.xdsoft_current,
.xdsoft_datetimepicker .xdsoft_timepicker .xdsoft_time_box > div > div.xdsoft_current {
    font-weight: 700;
    color: #ffffff;
    background: #33aaff;
    box-shadow: #178fe5 0 1px 3px 0 inset;
}
.xdsoft_datetimepicker .xdsoft_calendar td.xdsoft_other_month,
.xdsoft_datetimepicker .xdsoft_calendar td.xdsoft_disabled,
.xdsoft_datetimepicker .xdsoft_time_box > div > div.xdsoft_disabled {
    cursor: default;
    opacity: 0.5;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
}
.xdsoft_datetimepicker .xdsoft_calendar td.xdsoft_other_month.xdsoft_disabled {
    opacity: 0.2;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=20)";
}
.xdsoft_datetimepicker .xdsoft_calendar td:hover,
.xdsoft_datetimepicker .xdsoft_timepicker .xdsoft_time_box > div > div:hover {
    color: #ffffff !important;
    background: #ff8000 !important;
    box-shadow: none !important;
}
.xdsoft_datetimepicker .xdsoft_calendar td.xdsoft_current.xdsoft_disabled:hover,
.xdsoft_datetimepicker .xdsoft_timepicker .xdsoft_time_box > div > div.xdsoft_current.xdsoft_disabled:hover {
    color: #ffffff !important;
    background: #33aaff !important;
    box-shadow: #178fe5 0 1px 3px 0 inset !important;
}
.xdsoft_datetimepicker .xdsoft_calendar td.xdsoft_disabled:hover,
.xdsoft_datetimepicker .xdsoft_timepicker .xdsoft_time_box > div > div.xdsoft_disabled:hover {
    color: inherit !important;
    background: inherit !important;
    box-shadow: inherit !important;
}
.xdsoft_datetimepicker .xdsoft_calendar th {
    font-weight: 700;
    cursor: default;
    text-align: center;
    color: #999999;
}
.xdsoft_datetimepicker .xdsoft_copyright {
    font-size: 10px;
    float: none;
    clear: both;
    margin-left: 8px;
    color: #cccccc !important;
}
.xdsoft_datetimepicker .xdsoft_copyright a {
    color: #eeeeee !important
}
.xdsoft_datetimepicker .xdsoft_copyright a:hover {
    color: #aaaaaa !important
}
.xdsoft_time_box {
    position: relative;
    border: 1px solid #cccccc;
}
.xdsoft_scrollbar > .xdsoft_scroller {
    height: 20px;
    border-radius: 3px;
    background: #cccccc !important;
}
.xdsoft_scrollbar {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 7px;
    cursor: pointer;
}
.xdsoft_datetimepicker.xdsoft_rtl .xdsoft_scrollbar {
    right: auto;
    left: 0;
}
.xdsoft_scroller_box {
    position: relative;
}
.xdsoft_datetimepicker.xdsoft_dark {
    color: #cccccc;
    border-top: 1px solid #333333;
    border-right: 1px solid #333333;
    border-bottom: 1px solid #444444;
    border-left: 1px solid #333333;
    background: #000000;
    box-shadow: 0 5px 15px -5px rgba(255, 255, 255, 0.506);
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_timepicker .xdsoft_time_box {
    border-bottom: 1px solid #222222;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_timepicker .xdsoft_time_box > div > div {
    color: #999999;
    border-top: 1px solid #222222;
    background: #0a0a0a;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_label {
    background-color: #000000;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_label > .xdsoft_select {
    border: 1px solid #333333;
    background: #000000;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_label > .xdsoft_select > div > .xdsoft_option:hover {
    color: #000000;
    background: #007fff;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_label > .xdsoft_select > div > .xdsoft_option.xdsoft_current {
    color: #000000;
    background: #cc5500;
    box-shadow: #b03e00 0 1px 3px 0 inset;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_label i,
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_prev,
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_next,
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_today_button {
    background-image: url(data:image/png;base64,);
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_calendar td,
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_calendar th {
    color: #999999;
    border: 1px solid #222222;
    background: #0a0a0a;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_calendar th {
    background: #0e0e0e;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_calendar td.xdsoft_today {
    color: #cc5500;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_calendar td.xdsoft_highlighted_default {
    color: #000000;
    background: #ffe9d2;
    box-shadow: #ffb871 0 1px 4px 0 inset;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_calendar td.xdsoft_highlighted_mint {
    color: #000000;
    background: #c1ffc9;
    box-shadow: #00dd1c 0 1px 4px 0 inset;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_calendar td.xdsoft_default,
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_calendar td.xdsoft_current,
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_timepicker .xdsoft_time_box > div > div.xdsoft_current {
    color: #000000;
    background: #cc5500;
    box-shadow: #b03e00 0 1px 3px 0 inset;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_calendar td:hover,
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_timepicker .xdsoft_time_box > div > div:hover {
    color: #000000 !important;
    background: #007fff !important;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_calendar th {
    color: #666666;
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_copyright {
    color: #333333 !important
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_copyright a {
    color: #111111 !important
}
.xdsoft_datetimepicker.xdsoft_dark .xdsoft_copyright a:hover {
    color: #555555 !important
}
.xdsoft_dark .xdsoft_time_box {
    border: 1px solid #333333;
}
.xdsoft_dark .xdsoft_scrollbar > .xdsoft_scroller {
    background: #333333 !important;
}
.xdsoft_datetimepicker .xdsoft_save_selected {
    font-size: 13px;
    display: block;
    width: 100%;
    margin-top: 5px;
    color: #454551;
    border: 1px solid #dddddd !important;
}
.xdsoft_datetimepicker .blue-gradient-button {
    font-family: "museo-sans", "Book Antiqua", sans-serif;
    font-size: 12px;
    font-weight: 300;
    position: relative;
    height: 28px;
    padding: 4px 17px 4px 33px;
    color: #82878c;
    border: 1px solid #d7d8da;
    background: -moz-linear-gradient(top, #ffffff 0%, #f4f8fa 73%);
    /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #ffffff), color-stop(73%, #f4f8fa));
    /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top, #ffffff 0%, #f4f8fa 73%);
    /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(top, #ffffff 0%, #f4f8fa 73%);
    /* Opera 11.10+ */
    background: -ms-linear-gradient(top, #ffffff 0%, #f4f8fa 73%);
    /* IE10+ */
    background: linear-gradient(to bottom, #ffffff 0%, #f4f8fa 73%);
    /* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fff', 结束Colorstr='#f4f8fa', GradientType=0);
    /* IE6-9 */
}
.xdsoft_datetimepicker .blue-gradient-button:hover, .xdsoft_datetimepicker .blue-gradient-button:focus, .xdsoft_datetimepicker .blue-gradient-button:hover span, .xdsoft_datetimepicker .blue-gradient-button:focus span {
    color: #454551;
    background: -moz-linear-gradient(top, #f4f8fa 0%, #ffffff 73%);
    /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #f4f8fa), color-stop(73%, #ffffff));
    /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top, #f4f8fa 0%, #ffffff 73%);
    /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(top, #f4f8fa 0%, #ffffff 73%);
    /* Opera 11.10+ */
    background: -ms-linear-gradient(top, #f4f8fa 0%, #ffffff 73%);
    /* IE10+ */
    background: linear-gradient(to bottom, #f4f8fa 0%, #ffffff 73%);
    /* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#f4f8fa', 结束Colorstr='#FFF', GradientType=0);
    /* IE6-9 */
}
