/******/
/* 基础 */
/* ====================================================== 
    <!-- 基础 basic -->
/* ====================================================== */
::placeholder {
  color: #e6e6e8;
}

::selection,
::-moz-selection,
::-webkit-selection {
  color: #ffffff;
  background: #1f1f1f;
}

/* 避免默认的滚动条平滑效果 */
@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: inherit;
  }
}

html,
body {
  width: 100%;
  height: 100%;
}

html {
  font-size: 16px;
  /* Required, global font size */
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

*,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
a {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, Helvetica Neue, PingFang SC, Microsoft YaHei, Source Han Sans SC, Noto Sans CJK SC, WenQuanYi Micro Hei, sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.71428571429;
  color: #212121;
  background-color: transparent;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body.is-mobile {
  padding-top: 0;
}

p,
pre,
table,
blockquote {
  margin-bottom: 1.421875rem;
}

select {
  max-width: 100%;
}

.wp-block-quote,
blockquote {
  font-size: 1rem;
  line-height: 2;
  margin-top: 1.421875rem !important;
  padding: 0 1.75rem 1.25rem !important;
  border: 1px solid #f8f8f8 !important;
  border-left: 5px solid #ce3a3e !important;
  background: #ffffff;
}

.wp-block-quote::before,
blockquote::before {
  font-family: sans-serif;
  font-size: 5.25rem;
  line-height: 0.1;
  margin-right: 0.21875rem;
  content: "\201C";
  vertical-align: -2.055rem;
  color: #ce3a3e;
  /*左双引号的Unicode*/
}

strong {
  font-weight: 600;
}

hr {
  margin: 1.421875rem 0;
  border-color: #dcdcdc;
}

hr.black {
  border-color: rgba(255, 255, 255, 0.1);
}

a img {
  border: none;
}

img {
  max-width: 100%;
}

iframe {
  border: none !important;
}

code {
  font-family: "Inconsolata", "Monaco", "Consolas", "Andale Mono", "Bitstream Vera Sans Mono", "Courier New", Courier, monospace;
  display: inline-block;
  margin: 0 .2rem;
  padding: 0 .4rem;
  white-space: nowrap;
  color: #c7254e;
  border-radius: 3px;
  background-color: #f9f2f4;
}

pre {
  overflow: auto;
  margin: 1rem 0;
  padding: 0;
  white-space: pre;
  word-wrap: break-word;
  background: #ffffff;
}

pre.uix-precode {
  position: relative;
  white-space: normal;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.08);
}

pre.uix-precode label {
  font-family: sans-serif;
  font-size: 0.8125rem;
  font-weight: normal;
  position: absolute;
  top: 1rem;
  left: -5px;
  width: 60px;
  user-select: none;
  text-align: center;
  pointer-events: none;
  color: #dddddd;
}

pre.uix-precode code {
  font-family: "Inconsolata", "Monaco", "Consolas", "Andale Mono", "Bitstream Vera Sans Mono", "Courier New", Courier, monospace;
  font-size: 0.8125rem;
  line-height: 1.125rem;
  display: block;
  overflow-x: auto;
  max-height: 50vh;
  margin: 0 0 0 3rem;
  padding: 1rem;
  white-space: pre;
  color: #444444;
  border-left: 1px solid #d9d9d9;
  background: #ffffff;
}

pre.uix-precode.uix-precode--nolebel label {
  display: none;
}

pre.uix-precode.uix-precode--nolebel code {
  margin: 0;
  border: none;
}

figure {
  position: relative;
}

figure figcaption {
  font-size: 0.875rem;
  position: absolute;
  bottom: 0;
  overflow: auto;
  width: 100%;
  max-height: 100%;
  padding: 1.5rem 1rem 1rem;
  text-align: center;
  color: #ffffff;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.7) 0, rgba(0, 0, 0, 0.3) 70%, transparent);
}

/* ====================================================== 
   <!-- 品牌 brand -->
/* ====================================================== */
.uix-brand {
  font-size: 1.3125rem;
  font-weight: 700;
  float: left;
  overflow: hidden;
  max-width: 95%;
  white-space: nowrap;
  text-transform: uppercase;
  text-overflow: ellipsis;
}

.uix-brand img {
  max-height: 50px;
  margin-top: 0.75rem;
}

.uix-brand .uix-brand__description {
  font-size: 0.75rem;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
  white-space: nowrap;
  font-weight: 100;
  line-height: 1.2;
  width: 90%;
  padding: 0;
}

.uix-brand--mobile {
  display: none;
}

@media all and (max-width: 768px) {
  .uix-brand--mobile {
    display: block;
    text-align: center;
  }
  .uix-brand--mobile img {
    max-width: 90%;
    max-height: 100px;
    margin: 20px auto;
  }
  .uix-brand img {
    margin-bottom: 0.75rem;
  }
}

/* ====================================================== 
   <!-- 项目符号列表 bullet-list -->
/* ====================================================== */
/*
  * 1. 普通列表样式
  * 2. 普通数字有序列表
  * 3. 自定义圆形图标有序列表
  * 4. 大数字有序列表
  * 5. 按数字分步时间线
*/
ul,
ol {
  margin-bottom: 1.421875rem;
  list-style: none;
}

ul > li,
ol > li {
  margin-bottom: 0;
  transition: all 0.3s ease;
}

.uix-list::after {
  display: block;
  clear: both;
  content: '';
}

/*
 ---------------------------
 1. 普通列表样式
 ---------------------------
 */
ol.uix-list--normal,
ul.uix-list--normal,
.uix-list--normal ul,
.uix-list--normal ol {
  display: block;
  margin: 1.421875rem;
  padding-left: 0.875rem;
}

ol.uix-list--normal ul,
ol.uix-list--normal ol,
ul.uix-list--normal ul,
ul.uix-list--normal ol,
.uix-list--normal ul ul,
.uix-list--normal ul ol,
.uix-list--normal ol ul,
.uix-list--normal ol ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
}

ul.uix-list--normal,
.uix-list--normal ul {
  list-style-type: disc;
}

ol.uix-list--normal,
.uix-list--normal ol {
  list-style-type: decimal;
}

/*-- 默认的Noemal列表效果--*/
/* 兼容 Uix 简码（WordPress 插件）和阻止按钮链接 */
.uix-list--normal a:not(.uix-sc-btn):not(.wp-block-button__link):not(.uix-btn) {
  color: #ce3a3e;
}

.uix-list--normal a:not(.uix-sc-btn):not(.wp-block-button__link):not(.uix-btn):hover {
  text-decoration: underline;
  color: #de510e;
}

.uix-list--normal a:not(.uix-sc-btn):not(.wp-block-button__link):not(.uix-btn):hover img {
  text-decoration: none;
}

/*
 ---------------------------
 2. 普通数字有序列表
 ---------------------------
 */
ol.uix-list--numbered,
.uix-list--numbered ol {
  margin-left: 1rem;
  list-style: none;
  counter-reset: line;
}

ol.uix-list--numbered > li,
.uix-list--numbered ol > li {
  position: relative;
  margin-bottom: .5rem;
  margin-left: .5rem;
}

ol.uix-list--numbered > li::before,
.uix-list--numbered ol > li::before {
  font-size: 0.75rem;
  line-height: 22px;
  position: absolute;
  left: -2.25em;
  display: inline-block;
  width: 22px;
  height: 22px;
  margin-right: 0.5em;
  content: "0" counter(line);
  counter-increment: line;
  text-align: center;
  color: #ffffff;
  border-radius: 50%;
  background-color: #ce3a3e;
}

.uix-list--numbered.uix-list--numbered-1-digit > li::before {
  content: counter(line);
}

/* 
 ---------------------------
 3. 自定义圆形图标有序列表
 ---------------------------
 */
ol.uix-list--circle,
ul.uix-list--circle,
.uix-list--circle ol,
.uix-list--circle ul {
  margin-left: 1rem;
  list-style: none;
  counter-reset: line;
}

ol.uix-list--circle > li,
ul.uix-list--circle > li,
.uix-list--circle ol > li,
.uix-list--circle ul > li {
  position: relative;
  margin-bottom: .5rem;
  margin-left: 1rem;
  color: #212121;
}

ol.uix-list--circle > li::before,
ul.uix-list--circle > li::before,
.uix-list--circle ol > li::before,
.uix-list--circle ul > li::before {
  position: absolute;
  top: 50%;
  left: -1.5em;
  display: inline-block;
  width: 10px;
  height: 10px;
  content: '';
  counter-increment: line;
  transform: translateY(-50%);
  text-align: center;
  color: #ffffff;
  border-radius: 50%;
  background-color: #ce3a3e;
}

/*
 ---------------------------
 4. 大数字有序列表
 ---------------------------
 */
ol.uix-list--numbered-large,
.uix-list--numbered-large ol {
  list-style-type: none;
  /*删除原始编号*/
  counter-reset: item;
  /*如果全宽则删除*/
  text-align: justify;
}

ol.uix-list--numbered-large li,
.uix-list--numbered-large ol li {
  position: relative;
  display: block;
  min-height: 3.5rem;
  margin-left: 1.2rem;
  /*通用对齐方式*/
  padding-left: 2.5rem;
  list-style-position: outside;
  list-style-type: number;
  text-indent: 0;
}

ol.uix-list--numbered-large li::before,
.uix-list--numbered-large ol li::before {
  font-size: 3rem;
  line-height: 1;
  position: absolute;
  margin-left: -3.5rem;
  content: "0" counter(item) ".";
  counter-increment: item;
  color: #aeaeae;
}

.uix-list--numbered-large.uix-list--numbered-1-digit li::before {
  content: counter(item) ".";
}

ol.uix-list--numbered-large.uix-list--numbered-bg li,
.uix-list--numbered-large.uix-list--numbered-bg ol li {
  min-height: 70px;
  margin-bottom: 1rem;
  margin-left: 0;
  padding-left: 4.5rem;
  border-radius: 50px;
  background: #e4e1e1;
}

ol.uix-list--numbered-large.uix-list--numbered-bg li p,
.uix-list--numbered-large.uix-list--numbered-bg ol li p {
  line-height: 1.3;
  padding-top: 1.5rem;
  padding-right: 2rem;
}

ol.uix-list--numbered-large.uix-list--numbered-bg li::before,
.uix-list--numbered-large.uix-list--numbered-bg ol li::before {
  font-size: 1.5rem;
  line-height: 50px;
  top: 50%;
  width: 50px;
  height: 50px;
  margin-left: -4rem;
  content: "0" counter(item);
  transform: translateY(-50%);
  text-align: center;
  color: #ffffff;
  border-radius: 50%;
  background: #ce3a3e;
}

.uix-list--numbered-large.uix-list--numbered-bg.uix-list--numbered-1-digit li::before {
  content: counter(item);
}

/* 
 ---------------------------
 5. 按数字分步时间线
 ---------------------------
 */
.uix-list--numbered-step {
  margin-left: 1rem;
  list-style-type: none;
  counter-reset: step;
}

.uix-list--numbered-step li {
  position: relative;
  min-height: 75px;
  padding: 0 0 1.5rem 3.75rem;
  border-left: 2px solid #333333;
}

.uix-list--numbered-step li::before {
  font-size: 1rem;
  font-weight: 700;
  line-height: 48px;
  position: absolute;
  z-index: 2;
  top: 0;
  left: -26px;
  width: 50px;
  height: 50px;
  content: counters(step, ".") " ";
  counter-increment: step;
  text-align: center;
  color: #ffffff;
  border: 2px solid #333333;
  border-radius: 50%;
  background-color: #ce3a3e;
}

.uix-list--numbered-step li:last-child {
  border-left: none;
}

.uix-list--numbered-step li:last-child::before {
  content: '\2713';
}

@media all and (max-width: 768px) {
  .uix-list--numbered-step li {
    padding-left: 2.5rem;
  }
}

/* ================================================= ======
    <!-- 入口 entry -->
/* ================================================= ====== */
/*
  * 1. 间距
  * 2. 分享
  * 3. 标题
  * 4. 主要内容
  * 5. 元信息
*/
/*
 ---------------------------
 1. 间距
 ---------------------------
 */
.uix-entry__box {
  padding: 2.84375rem 0 1.75rem;
  /*-- WordPress风格--*/
}

.uix-entry__box.uix-entry__box--top {
  padding: 2.84375rem 0 0;
}

.uix-entry__box.uix-entry__box--mark a {
  color: #ce3a3e;
}

.uix-entry__box.uix-entry__box--mark a:hover {
  color: #de510e;
}

.uix-entry__box .post-navigation {
  width: 100%;
}

.uix-entry__box .nav-previous,
.uix-entry__box .nav-next {
  display: inline-block;
  width: 50%;
}

/*
 ---------------------------
 2. 分享
 ---------------------------
 */
.uix-entry__box__share .uix-btn {
  margin: 0 .5rem 1rem;
}

/*
 ---------------------------
 3. 标题
 ---------------------------
 */
.uix-entry__box__title {
  padding-bottom: 1.75rem;
}

@media all and (max-width: 768px) {
  .uix-entry__box__title {
    padding-bottom: 0.4375rem;
  }
}

/*
 ---------------------------
 4. 主要内容
 ---------------------------
 */
.uix-entry__content {
  word-wrap: break-word;
  /* 对齐宽类和全类 */
  /* (使用 WordPress 默认类名) */
}

.uix-entry__content pre:not(.uix-precode) {
  padding: .3em .7em;
  border: 1px solid #d9d9d9;
  border-left: 5px solid #ce3a3e;
  background: #ffffff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.08);
}

.uix-entry__content img {
  height: auto !important;
}

.uix-entry__content ul li img {
  height: inherit !important;
  /* 用于图库网格 */
}

.uix-entry__content iframe {
  max-width: 100% !important;
}

.uix-entry__content img.alignfull,
.uix-entry__content img.alignwide,
.uix-entry__content .alignfull img,
.uix-entry__content .alignwide img {
  width: 100vw !important;
  max-width: 100vw;
  margin-right: calc(-100vw / 2 + 100% / 2);
  margin-left: calc(-100vw / 2 + 100% / 2);
}

@media only screen and (min-width: 950px) {
  .uix-entry__content img.alignwide,
  .uix-entry__content .alignwide img {
    max-width: inherit;
  }
}

/*
 ---------------------------
 5. 元信息
 ---------------------------
 */
ul.uix-entry__meta {
  font-size: 0.75rem;
  font-style: italic;
  line-height: 1.33333333333;
  display: block;
  width: 500px;
  color: #999999;
}

ul.uix-entry__meta li {
  display: inline-block;
  /*要求*/
  margin-bottom: 0.62890625rem;
}

ul.uix-entry__meta li::before {
  display: inline-block;
  margin-right: 0.4375rem;
  content: '/';
}

ul.uix-entry__meta li a {
  color: #999999;
}

ul.uix-entry__meta.uix-entry__meta--centered {
  margin: 0 auto;
}

ul.uix-entry__meta.uix-entry__meta--wrap li {
  display: block;
}

ul.uix-entry__meta.uix-entry__meta--wrap li::before {
  display: none;
}

ul.uix-entry__meta.uix-entry__meta--wrap li strong {
  font-size: 0.875rem;
  display: inline-block;
  width: 130px;
  padding-right: 0.4375rem;
  vertical-align: top;
  color: #c9c9c9;
}

ul.uix-entry__meta.uix-entry__meta--wrap li span {
  display: inline-block;
  width: calc(100% - 130px - 0.875rem);
}

ul.uix-entry__meta.uix-entry__meta--wrap li a {
  color: #ce3a3e;
}

ul.uix-entry__meta.uix-entry__meta--wrap li a:hover {
  color: #de510e;
}

ul.uix-entry__meta:not(.uix-entry__meta--wrap) li:first-child::before {
  display: none;
}

/* ================================================= ======
    <!-- 页脚 footer -->
/* ================================================= ====== */
.uix-footer__container {
  font-size: 0.75rem;
  padding: 3rem 0;
}

/* ul li ul li -> 全部在一行 */
.uix-footer__menu,
.uix-footer__menu ul,
.uix-footer__menu li {
  position: relative;
  display: inline;
}

.uix-footer__menu li a {
  padding: 0 0.4375rem;
  border-right: 1px solid #e1e1e1;
}

.uix-footer__menu li:last-child a {
  border: none;
}

@media all and (max-width: 768px) {
  .uix-footer__container .uix-f-l,
  .uix-footer__container .uix-f-r {
    float: none !important;
  }
}

/* ================================================= ======
    <!-- 标题区 header-area -->
/* ================================================= ====== */
/*
  * 1. 默认
  * 2. 主题 - 白色
*/
/*
 ---------------------------
 1. 默认
 ---------------------------
 */
/* 不要使用“margin”或“padding”来设置“.uix-header__container” */
.uix-header__container {
  position: fixed;
  z-index: 90000;
  top: 0;
  left: 0;
  width: 100%;
  border-bottom: 2.15px solid rgba(255, 255, 255, 0.2);
  background-color: #ce3a3e;
  background-image: linear-gradient(to right, #ce3a3e 0%, #c04645 20%, #de510e 100%);
  box-shadow: 0 5px 40px 0 rgba(0, 0, 0, 0.08);
  /*
     ---------------------------
     2. 主题 - 白色
     ---------------------------
     */
}

.uix-header__container.uix-header__container--overlay {
  background: rgba(255, 255, 255, 0.15);
  background: linear-gradient(to right, rgba(255, 255, 255, 0.15) 0%, rgba(255, 255, 255, 0.15) 100%);
}

.uix-header__container.uix-header__container--overlay.is-fixed {
  background-color: #ce3a3e;
  background-image: linear-gradient(to right, #ce3a3e 0%, #c04645 20%, #de510e 100%);
}

/* 使用 WordPress 查看网站时显示工具栏 */
.admin-bar .uix-header__container {
  top: 32px;
}

/* 管理栏在使用 WordPress 的小型设备上变得更高 */
@media all and (max-width: 782px) {
  .admin-bar .uix-header__container {
    top: 0;
  }
}

/* 不要使用“margin”或“padding”来设置“.uix-header” */
.uix-header {
  /* IE 的浮动层保护 */
  background: url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7);
}

.uix-header .uix-menu__right-box {
  float: right;
}

/* 标题占位符，防止浮动元素影响布局 */
.uix-header__placeholder {
  display: block;
  visibility: hidden;
  width: 100%;
  height: 75px;
  background: none;
}

@media all and (max-width: 768px) {
  .uix-header__container {
    position: relative;
  }
  .uix-header__container.uix-header__container--overlay {
    position: relative;
    background-color: #ce3a3e;
    background-image: linear-gradient(to right, #ce3a3e 0%, #c04645 20%, #de510e 100%);
  }
  /* 标题占位符，防止浮动元素影响布局 */
  .uix-header__placeholder {
    height: 0;
  }
}

/* ================================================= ======
     <!-- 辅助类 helper-classes -->
/* ================================================= ====== */
/*
  * 1. 内容块
  * 2. 浮动元素之后的元素将围绕它流动
  * 3. 阴影效果
  * 4. 平滑过渡效果
  * 5. 元素浮动
  * 6. 元素对齐
  * 7. 用省略号截断字符串
  * 8. 垂直或水平对齐的块
  * 9. 指定是否裁剪内容
  * 10. 指定默认高度
  * 11. 隐藏移动或桌面设备上的元素
  * 12. 提示气泡
  * 13. 图像悬停叠加效果
  * 14. 定位
  * 15. 元素隐藏样式
  * 16. 边界半径
  * 17. 对齐元素的宽类和全类
  * 18. HTML 公开摘要元素
*/
/*
 ---------------------------
 1. 内容块
 ---------------------------
 */
.uix-relative {
  position: relative;
}

.uix-relative--inline {
  position: relative;
  display: inline-block;
  /* 防止内容溢出 */
  max-width: 100%;
  /* 表现得像大多数内联块元素 */
  vertical-align: middle;
  /*
     * 与 Webkit 中的叠加层和过渡相结合时，强制硬件加速而不创建新的堆叠上下文来修复 1px 故障
     */
  -webkit-backface-visibility: hidden;
}

/* 裁剪子元素 */
.uix-relative--inline-clip {
  overflow: hidden;
}

/*----*/
.uix-display-none {
  display: none !important;
}

.uix-display-inline {
  display: inline !important;
}

.uix-display-inline-block {
  display: inline-block !important;
}

.uix-display-block {
  display: block !important;
}

.uix-display-flex {
