/* ================================================= ======
    <!-- 导航 navs -->
/* ================================================= ====== */
/*
  * 1. 基地导航
  * 2.线路导航
  * 3. 分隔线导航
  * 4. 下拉菜单
  * 5. 响应式下拉导航
*/
.uix-nav {
  /*
     ---------------------------
     1. 基础导航
     ---------------------------
     */
  /*
     ---------------------------
     2. 路线导航
     ---------------------------
     */
  /*
     ---------------------------
     3. 分隔线导航
     ---------------------------
     */
  /*
     ---------------------------
     4. 下拉菜单
     ---------------------------
     */
  /*
     ---------------------------
     5. 响应式下拉导航
     ---------------------------
     */
}

.uix-nav::before {
  line-height: 0;
  display: table;
  content: "";
}

.uix-nav::after {
  line-height: 0;
  display: table;
  clear: both;
  content: "";
}

.uix-nav ul {
  font-size: 0;
  /* 争夺内联块元素之间的空间 */
  margin: 0;
}

.uix-nav ul li {
  font-size: 0.875rem;
  /* 争夺内联块元素之间的空间 */
  position: relative;
  display: inline-block;
  margin: 0 1rem 0 0;
  padding: 0;
}

.uix-nav ul li a {
  display: block;
  padding: .3rem 1.5rem;
  border: #e8e8e8 1px solid;
  border-radius: 3px;
}

.uix-nav ul li a:hover {
  color: #232323;
}

.uix-nav ul li.is-active a, .uix-nav ul li.current-cat a {
  color: #ce3a3e;
  border-color: #ce3a3e;
}

.uix-nav.uix-t-r ul li {
  margin: 0 0 0 1rem;
}

.uix-nav.uix-nav--line ul li {
  font-size: 1.25rem;
  /* 争夺内联块元素之间的空间 */
  margin: 0;
  padding: 1rem 2.5rem;
}

.uix-nav.uix-nav--line ul li a {
  padding: 0;
  border: none;
}

.uix-nav.uix-nav--line ul li.is-active, .uix-nav.uix-nav--line ul li.current-cat {
  box-shadow: 0 3px 0 0 #ce3a3e;
}

.uix-nav.uix-nav--separation ul li {
  margin: 0;
  padding: 0;
}

.uix-nav.uix-nav--separation ul li::before {
  position: absolute;
  top: 50%;
  left: 0;
  content: '/';
  transform: translateY(-50%);
  opacity: .3;
}

.uix-nav.uix-nav--separation ul li:first-child::before {
  display: none;
}

.uix-nav.uix-nav--separation ul li a {
  padding: .5rem 1.5rem;
  border: none;
}

.uix-nav ul li {
  /*-- 展开 --*/
}

.uix-nav ul li ul {
  position: absolute;
  z-index: 1;
  top: 100%;
  left: 0;
  overflow: hidden;
  width: 200px;
  max-height: 0;
  transition: all 0.3s ease;
  transform: perspective(400) rotate3d(1, 0, 0, -90deg);
  transform-origin: 50% 0;
  border-radius: 8px;
  background: #ffffff;
  box-shadow: rgba(0, 0, 0, 0.18) 0 19px 38px, rgba(0, 0, 0, 0.08) 0 15px 12px;
}

.uix-nav ul li ul li,
.uix-nav ul li ul a {
  margin: 0;
  padding: 0;
  border: none;
  box-shadow: none;
}

.uix-nav ul li ul li {
  display: block;
  padding: .3rem 1rem;
}

.uix-nav ul li ul li::before {
  display: none;
}

.uix-nav ul li ul li:first-child {
  margin-top: .5rem;
}

.uix-nav ul li ul li:last-child {
  margin-bottom: .5rem;
}

.uix-nav ul li:hover ul {
  max-height: 1000px;
  transform: perspective(400) rotate3d(0, 0, 0, 0);
}

.uix-nav.uix-nav--dropdown-mobile .uix-nav--dropdown-mobile__togglelabel {
  display: none;
}

.uix-nav.uix-nav--dropdown-mobile .uix-nav--dropdown-mobile__togglelabel ~ input {
  display: none;
}

@media all and (max-width: 768px) {
  .uix-nav {
    /*
         ---------------------------
         5. 响应式下拉导航
         ---------------------------
         */
  }
  .uix-nav.uix-nav--dropdown-mobile {
    /* 将其包装在媒体查询中以防止在更改屏幕大小和检查输入时失败 */
  }
  .uix-nav.uix-nav--dropdown-mobile .uix-nav--dropdown-mobile__togglelabel {
    display: block;
    cursor: pointer;
    user-select: none;
  }
  .uix-nav.uix-nav--dropdown-mobile > ul {
    box-shadow: rgba(0, 0, 0, 0.18) 0 19px 38px, rgba(0, 0, 0, 0.08) 0 15px 12px;
    margin-top: -0.203125rem;
  }
  .uix-nav.uix-nav--dropdown-mobile > :checked ~ ul {
    display: none;
  }
  .uix-nav.uix-nav--dropdown-mobile li {
    display: block;
    width: 100%;
    padding: .2rem 0;
    text-align: left;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
  .uix-nav.uix-nav--dropdown-mobile li::before {
    display: none;
  }
  .uix-nav.uix-nav--dropdown-mobile li:not(:last-child) {
    margin-right: 0;
  }
  .uix-nav.uix-nav--dropdown-mobile li:not(:last-child)::after {
    margin-left: 0;
    content: '';
  }
  .uix-nav.uix-nav--dropdown-mobile li a {
    position: relative;
    display: block;
    padding: .5rem 1.5rem;
    border: none;
  }
  .uix-nav.uix-nav--dropdown-mobile li a::after {
    font-family: 'Font Awesome 5 Free';
    font-size: 1rem;
    font-weight: 600;
    position: absolute;
    top: 50%;
    right: .5rem;
    float: right;
    content: "\f105";
    transform: translateY(-50%);
  }
}

