/* ====================================================== 
   <!-- 间距 spacing -->
/* ====================================================== */
.uix-spacing--s {
  position: relative;
  padding: 3rem 0;
}

.uix-spacing--m {
  position: relative;
  padding: 6.5625rem 0;
}

.uix-spacing--l {
  position: relative;
  padding: 11.375rem 0;
}

.uix-spacing--no-top {
  padding-top: 0 !important;
}

.uix-spacing--no-bottom {
  padding-bottom: 0 !important;
}

.uix-spacing--googlemap {
  padding-top: 0 !important;
}

/* 没有间距 */
.uix-spacing--no {
  margin: 0 !important;
  /* 兼容 <p>, <h1>, <h2>, <h3>... */
  padding: 0 !important;
}

@media all and (max-width: 768px) {
  .uix-spacing--s {
    padding: 1.5rem 0;
  }
  .uix-spacing--m {
    padding: 3.28125rem 0;
  }
  .uix-spacing--l {
    padding: 5.6875rem 0;
  }
  .uix-spacing--no-top {
    padding-top: 0;
  }
  .uix-spacing--no-bottom {
    padding-bottom: 0;
  }
  .uix-spacing--googlemap {
    padding-top: 0;
  }
  .uix-spacing--no {
    padding: 0;
  }
}

/* ====================================================== 
   <!-- 标签 tag -->
/* ====================================================== */
.uix-tags-box::before {
  display: block;
  clear: both;
  content: '';
}

.uix-tags-box > a {
  font-size: 0.875rem;
  display: inline-block;
  margin-bottom: 0.175rem;
  padding: 0.04375rem 0.109375rem;
}

.uix-tags-box > a::after {
  display: inline-block;
  margin-left: 0.546875rem;
  content: '/';
  opacity: 0.5;
}

.uix-tags-box > a:last-child:after {
  display: none;
}

.uix-tags-box.uix-tags-box--has-border > a {
  border: 1px solid #e6e6e6;
}

/* ================================================= ======
    <!-- 排版 typography -->
/* ================================================= ====== */
/*
  * 1. 一般
  * 2. 超链接
  * 3. 标题 (h1,h2,h3,h4,h5,h6)
  * 4.文字效果
  * 5. 首字下沉
  * 6. 竖排文字
*/
/*
  --------------------------
  1. 通用
  --------------------------
  */
a,
h1,
h2,
h3,
h4,
h5,
h6,
.uix-typo--h1,
.uix-typo--h2,
.uix-typo--h3,
.uix-typo--h4,
.uix-typo--h5,
.uix-typo--h6 {
  transition: all 0.3s ease;
}

/*
 ---------------------------
 2. 超链接
 ---------------------------
 */
a {
  text-decoration: none;
  color: #474747;
}

a:hover {
  text-decoration: none;
  color: #000000;
}

/*
 ---------------------------
 3. 标题 (h1,h2,h3,h4,h5,h6)
 ---------------------------
 */
h1,
h2,
h3,
h4,
h5,
h6,
.uix-typo--h1,
.uix-typo--h2,
.uix-typo--h3,
.uix-typo--h4,
.uix-typo--h5,
.uix-typo--h6 {
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 0.625rem;
  /* 连字和上下文形式是组合字形以产生更协调的形式的方式 */
  padding: 0 0 0.546875rem;
  word-wrap: break-word;
  color: #212121;
  font-variant-ligatures: common-ligatures;
}

h1 {
  font-size: 3.5rem;
  line-height: 1.14285714286;
}

h2 {
  font-size: 2.5rem;
  line-height: 1.2;
}

h3 {
  font-size: 1.75rem;
  line-height: 1.25;
}

h4 {
  font-size: 1.25rem;
  line-height: 1.33333333333;
}

h5 {
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.33333333333;
}

h6 {
  font-size: 0.75rem;
