/*
 ---------------------------
 5. 首字下沉
 ---------------------------
 */
.uix-typo--dropcap::first-letter,
.uix-typo--dropcap > p:first-of-type::first-letter {
  font-size: 3.5rem;
  line-height: 1;
  display: block;
  float: left;
  margin-right: 10px;
  margin-bottom: -2px;
}

/*
 ---------------------------
 6. 竖排文字
 ---------------------------
 */
.uix-typo--vertical {
  /* 定义一行中文本字符的方向 */
}

.uix-typo--vertical:not(.uix-typo--vertical-characters-line) {
  line-height: 0;
  /* 修复旋转后文字左倾的问题 */
  transform: translateX(0.5rem);
  writing-mode: vertical-rl;
}

.uix-typo--vertical.uix-typo--vertical-characters-line {
  line-height: 1;
  width: 1px;
  white-space: pre-wrap;
  /* 这是用于显示包括 Firefox 在内的空格 */
  word-wrap: break-word;
}

/* ====================================================== 
   <!-- 视频 Video -->
/* ====================================================== */
/* 直接嵌入网页或通过滑块嵌入 */
.uix-video,
.uix-video__slider {
  visibility: hidden;
}

/* 当某些移动设备浏览器无法自动播放视频时显示封面和播放按钮 */
.uix-video__cover .uix-video__cover__placeholder {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-attachment: scroll;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.uix-video__cover .uix-video__cover__playbtn {
  line-height: 64px;
  position: absolute;
  z-index: 11;
  top: 50%;
  left: 50%;
  display: none;
  width: 64px;
  height: 64px;
  margin-top: -32px;
  margin-left: -32px;
  border: 3px solid #ffffff;
  border-radius: 50%;
  text-shadow: 1px 0 3px rgba(0, 0, 0, 0.3);
}

.uix-video__cover .uix-video__cover__playbtn::before {
  font-family: 'Font Awesome 5 Free';
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1;
  display: inline-block;
  margin: 15px 15px 15px 1.3rem;
  content: "\f04b";
  cursor: pointer;
  color: #ffffff;
}

.uix-video__slider {
  visibility: visible;
}

/* 视频播放按钮 */
.uix-video__btn-play {
  font-size: 3rem;
  line-height: 44px;
  position: absolute;
  z-index: 11;
  top: 50%;
  left: 50%;
  display: none;
  width: 44px;
  height: 44px;
  margin-top: -22px;
  margin-left: -22px;
  text-shadow: 1px 0 3px rgba(0, 0, 0, 0.3);
}

.uix-video__btn-play::before {
  font-family: 'Font Awesome 5 Free';
  font-weight: 600;
  display: inline-block;
  content: "\f021";
  cursor: pointer;
  color: #ffffff;
}

[data-video-iframe] {
  display: none;
}

/* 视频背景的外部容器 */
.uix-video__container-bg {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

@media all and (max-width: 768px) {
  .uix-video__container-bg {
    height: auto;
  }
}

/* 覆盖 videojs 的默认按钮 */
.video-js .vjs-big-play-button {
  line-height: 64px;
  position: absolute;
  z-index: 11;
  top: 50%;
  left: 50%;
  width: 64px;
  height: 64px;
  margin-top: -32px;
  margin-left: -32px;
  border: 3px solid #ffffff;
  border-radius: 50%;
  text-shadow: 1px 0 3px rgba(0, 0, 0, 0.3);
}

.video-js .vjs-big-play-button::before {
  font-family: 'Font Awesome 5 Free';
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1;
  display: inline-block;
  margin: 15px 15px 15px 1.3rem;
  content: "\f04b";
  cursor: pointer;
  transform: translateY(-3px);
  color: #ffffff;
}

.video-js .vjs-big-play-button .vjs-icon-placeholder,
.video-js .vjs-big-play-button .vjs-control-text {
  display: none;
}
