 2. Captcha
 ---------------------------
 */
.custom-captcha-code-str {
  background-color: #F2F2F2;
  font-size: 20px;
  text-align: center;
  padding: .7rem 1.3rem;
  display: inline-block;
  margin-top: .2rem;
  pointer-events: none;
  user-select: none;
